import React, { FC } from 'react';
import { InputGroupInline, InputProps, InputType } from 'summer';
import { CLASS_NAME } from '../../../../models/dtos/ger/especie';

const EspecieIS: FC<InputProps> = props => {
  return (
    <InputGroupInline
      {...props}
      isPrependLabel={true}
      inputSearchProps={{
        ...props.inputSearchProps,
        columns: [
          {
            dataField: 'abreviacao',
            text: 'Abreviação',
          },
          {
            dataField: 'descricao',
            text: 'Descrição',
          },
        ],
        dtoClassName: CLASS_NAME,
        searchViewId: 'EspecieSearchView',
      }}
      label="Espécie"
      placeholder="Informe a Espécie"
      type={InputType.INPUT_SEARCH}
    />
  );
};

export default EspecieIS;
