import React from 'react';
import { useForm } from 'react-hook-form';
import { RouteComponentProps } from 'react-router';
import { Col, CustomInput, Row } from 'reactstrap';
import { EditForm, EditViewProps, getSelectedItem, InputGroupInline, WindowComponent } from 'summer';
import { CLASS_NAME, ConfigLancamentoReducaoZ } from '../../../../models/dtos/mlf/configLancamentoReducaoZ';
import EspecieIS from '../../../components/ger/especieIS/EspecieIS';

interface ConfigLancamentoReducaoZEditViewProps extends EditViewProps<ConfigLancamentoReducaoZ> {}

const ConfigLancamentoConhecimentoTransportEditView: React.FC<ConfigLancamentoReducaoZEditViewProps & RouteComponentProps> = props => {
  const selectedItem = getSelectedItem(props);
  const formProps = useForm<any>({
    defaultValues: selectedItem,
    mode: 'onBlur',
  });

  return (
    <WindowComponent title="Manutenção de Configuração de Lançamento de Conhecimento de Transporte">
      <EditForm formProps={formProps} editViewProps={props} dtoClassName={CLASS_NAME}>
        <Row>
          <Col>
            <InputGroupInline
              formProps={{ ...formProps, validation: { maxLength: 120, required: true } }}
              id="descricao"
              isPrependLabel={true}
              label="Descrição"
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <EspecieIS id="especie" formProps={formProps} />
          </Col>
        </Row>
        <Row>
          <Col>
            <CustomInput type="switch" id="sequenciarCoo" name="sequenciarCoo" label="Sequenciar Número de COO" innerRef={formProps.register} />
          </Col>
        </Row>
      </EditForm>
    </WindowComponent>
  );
};

export default ConfigLancamentoConhecimentoTransportEditView;
